*
{
    margin: 0;
    padding: 0;
    font-family: Verdana,sans-serif;
    box-sizing: border-box;
}
.footer
{
    padding: 20px 30px;
    width: 100%;
    background: #eee;
}
.footer .container
{
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.footer .container .detail_box
{
    width: 350px;
}
.footer .container .detail_box .header .logo img
{
    width: 160px;
    cursor: pointer;
    transition: 0.5s;
}
.footer .container .detail_box .header .logo img:hover
{
    opacity: 1;
}
.footer .container .box
{
   margin-top: 20px;
}
.footer .container .box .bottom
{
    margin-top: 10px;
}
.footer .container .box .bottom ul
{
    margin-top: 10px;
}
.footer .container .box .bottom ul li
{
    margin-top: 10px;
    color: #222;
    list-style: none;
    font-size: 14px;
    cursor: pointer;
    transition: 0.5s;
}
.footer .container .box .bottom ul li:hover
{
    color: #2396f3;
}
.footer .container .box .header h2
{
    color: #232323;
    font-size: 18px;
}
.footer .container .box .header h2 span
{
    border-bottom: 2px solid #2396f3;
}
.footer .container .detail_box .bottom
{
    margin-top: 15px;
}
.footer .container .detail_box .bottom .detail p
{
    font-size: 16px;
    color: #2396f3;
    font-weight: 600;
    /*margin-right: 30%;*/
}
.footer .container .detail_box .bottom .address
{
    margin-top: 10px;
}
.footer .container .bottom .address p
{
    font-size: 14px;
}
.footer .container .bottom .address p span
{
    color: #2396f3;
    font-weight: 600;
}
.footer .container .detail_box .bottom .contact
{
    margin-left: -50px;
    margin-top: -30px;
}
.footer .container .bottom .contact p
{
    font-size: 14px;
}
.footer .container .bottom .contact p span
{
    color: #2396f3;
    font-weight: 600;
}