*
{
    padding: 0;
    margin: 0;
    font-family: Helvetica, sans-serif;
    box-sizing: border-box;
}
.cart
{
    padding: 30px 40px;
    width: 100%;
    background: #fff;
}
.cart h2
{
    color: #232323;
    font-size: 42px;
    text-transform: capitalize;
    font-weight: 600;
    letter-spacing: 1px;
}
.cart .empty_cart
{
    width: 100%;
    text-align: center;
}
.cart .empty_cart h2
{
    color: #dc143c;
    margin-top: 20px;
    font-size: 22px;
    text-transform: capitalize;
    letter-spacing: 1px;
}
.cart .empty_cart button
{
    margin-top: 20px;
    padding: 10px 20px;
    cursor: pointer;
    transition: 0.5s;
    border: 1px solid #2396f3;
    background: #2396f3;
    color: #fff;
    font-weight: 600;
    outline: none;
    border-radius: 4px;
}
.cart .empty_cart button:hover
{
    background: #fff;
    color: #2396f3;
}
.cart .user_login
{
    width: 100%;
    text-align: center;
}
.cart .empty_cart h2
{
    color: #dc143c;
    margin-top: 20px;
    font-size: 22px;
    text-transform: capitalize;
    letter-spacing: 1px;
}
.cart .user_login button
{
    margin-top: 20px;
    padding: 10px 20px;
    cursor: pointer;
    transition: 0.5s;
    border: 1px solid #2396f3;
    background: #2396f3;
    color: #fff;
    font-weight: 600;
    outline: none;
    border-radius: 4px;
}
.cart .user_login button:hover
{
    background: #fff;
    color: #2396f3;
}
.cart .cart_product
{
    margin-top: 10px;
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.cart .cart_product .box
{
    width: 200px;
    padding: 10px 20px;
    border: 2px solid #eee;
    margin-left: 30px;
    overflow: hidden;
    margin-top: 10px;
    cursor: pointer;
    transition: 0.5s;
}
.cart .cart_product .box:hover
{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.cart .cart_product .box .img_box img
{
    width: 150px;
}
.cart .cart_product .box .detail
{
    margin-top: 10px;
}
.cart .cart_product .box .detail h3
{
    margin-top: 5px;
    color: #222222;
    font-size: 14px;
}
.cart .cart_product .box .detail p
{
    margin-top: 10px;
    color: #232323;
    font-size: 12px;
}
.cart .cart_product .box .detail .qte
{
    justify-content: center;
    margin-top: 10px;
    display: flex;
}
.cart .cart_product .box .detail .qte button
{
    padding: 10px 20px;
    border: none;
    outline: none;
    background: #dc143c;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
}
.cart .cart_product .box .detail .qte input
{
    text-align: center;
    border: none;
    outline: none;
    background: none;
    width: 60px;
}
.cart .cart_product .box .detail .rmv_button
{
    margin-top: 10px;
    padding: 10px 20px;
    border: 1px solid #dc143c;
    background: #dc143c;
    color: #fff;
    margin-left: 30px;
    transition: 0.5s;
    cursor: pointer;
}
.cart .cart_product .box .detail .rmv_button:hover
{
    background: #fff;
    color: #dc143c;
}
.cart .totalall h3
{
    margin-top: 10px;
    color: #222222;
}
.cart .totalall h3 span
{
    color: #2396f3;
    font-weight: 600;
}
.cart .checkout
{
    margin-top: 15px;
    width: 100%;
    justify-content: center;
    align-items: center;
}
.cart .checkout button
{
    width: 100%;
    text-align: center;
    padding: 10px 20px;
    border-radius: 5px;
    background: #2396f3;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
    transition: 0.5s;
    border: none;
    outline: none;
}
.cart .checkout button:hover
{
    background: #232323;
}