*
{
    margin: 0;
    padding: 0;
    font-family: arial,sans-serif;
    box-sizing: border-box;
}
body
{
    background: #f5f5f5;
}
.auth
{
    justify-content: center;
    align-items: center;
    display: flex;
    min-height: 100vh;
}
.auth .container
{
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    padding: 30px;
    width: 400px;
    text-align: center;
}
.auth .container h2
{
    text-align: center;
    font-size: 32px;
    color: #2196f3;
    text-transform: uppercase;
    font-weight: 800;
    letter-spacing: 1px;
    font-family: 'helvetica';
}
.auth .container .icon
{
    font-size: 22px;
    margin-top: 20px;
    color: #383bdf;

}
.auth .container .form
{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}
.auth .container .form .box
{
    margin-top: 20px;
}
.auth .container .form .box input
{
    width: 100%;
    padding: 12px;
    border: 1px solid #bdbdbd;
    outline: none;
}
.auth .container .form button
{
    margin-top: 20px;
    padding: 12px;
    background: #2196f3;
    border: none;
    border: 1px solid #2196f3;
    cursor: pointer;
    outline: none;
    border-radius: 6px;
    color: #fff;
    transition: 0.5s;
}
.auth .container .form button:hover
{
    background: none;
    color: #2196f3;
}
.auth .container .form p
{
    margin-top: 10px;
    text-align: center;
    font-size: 14px;
}